  },
  // ÌéòÏù¥ÏßÄ Î°úÎìú Ïãú ÏûêÎèôÏúºÎ°ú Î™®Îã¨ ÌëúÏãú (ÏõêÌïúÎã§Î©¥)
  mounted () {
    setTimeout(() => {
      this.showModal = true
    }, 1000) // 1Ï¥à ÌõÑÏóê Î™®Îã¨ ÌëúÏãú
  }
}

window.addEventListener('scroll', function () {
  const scrollPosition = window.scrollY
  const titleContainer = document.querySelector('.title-container')
  const titleText = document.querySelector('.title-container h1')
  const accountName = document.querySelector('.title-container .account-name')

  // Í∏∞Î≥∏ ÌÅ¨Í∏∞ÏôÄ ÏµúÏÜå ÌÅ¨Í∏∞ ÏÑ§Ï†ï
  const maxHeight = 120 // ÏãúÏûë ÎÜíÏù¥
  const minHeight = 60 // ÏµúÏÜå ÎÜíÏù¥
  const maxFontSize = 2.8 // ÏãúÏûë Ìè∞Ìä∏ ÌÅ¨Í∏∞ (rem)
  const minFontSize = 1.8 // ÏµúÏÜå Ìè∞Ìä∏ ÌÅ¨Í∏∞ (rem)

  // Ïä§ÌÅ¨Î°§ ÏúÑÏπòÏóê Îî∞Îùº Í≥ÑÏÇ∞ (300px Ïä§ÌÅ¨Î°§ÌïòÎ©¥ ÏµúÏÜå ÌÅ¨Í∏∞Ïóê ÎèÑÎã¨)
  const shrinkRatio = Math.max(0, Math.min(1, scrollPosition / 300))

  // ÎÜíÏù¥ÏôÄ Ìè∞Ìä∏ ÌÅ¨Í∏∞ Í≥ÑÏÇ∞
  const newHeight = maxHeight - shrinkRatio * (maxHeight - minHeight)
  const newFontSize = maxFontSize - shrinkRatio * (maxFontSize - minFontSize)

  // Ïä§ÌÉÄÏùº Ï†ÅÏö©
  titleContainer.style.height = newHeight + 'px'
  titleText.style.fontSize = newFontSize + 'rem'

  // Í≥ÑÏ†ïÎ™ÖÎèÑ ÎπÑÏú®Ïóê ÎßûÍ≤å Ï∂ïÏÜå
  accountName.style.fontSize = (1.1 - shrinkRatio * 0.3) + 'rem'

  // Ìå®Îî©ÎèÑ Ï°∞Ï†àÌï¥ÏÑú Ï†ÑÏ≤¥Ï†ÅÏù∏ Í∑†Ìòï Ïú†ÏßÄ
  titleContainer.style.paddingBottom = (35 - shrinkRatio * 15) + 'px'
})
